.node {
  width: 25px;
  height: 25px;
  outline: 1px solid rgb(25, 26, 25);
  display: inline-block;
  margin-bottom: -4px;
}

.node-finish {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url(data:image/png;base64,);
}

.node-start {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url(/pathfindingVisualizer/static/media/start.de0d5820.png);
}

.node-visited {
  -webkit-animation-name: visitedAnimation;
          animation-name: visitedAnimation;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes visitedAnimation {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    background-color: rgb(24, 10, 10);
    border-radius: 100%;
  }

  50% {
    background-color: rgb(113, 26, 117);
  }

  75% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgb(241, 0, 134);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgb(245, 130, 167);
  }
}

@keyframes visitedAnimation {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    background-color: rgb(24, 10, 10);
    border-radius: 100%;
  }

  50% {
    background-color: rgb(113, 26, 117);
  }

  75% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgb(241, 0, 134);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgb(245, 130, 167);
  }
}

.node-wall {
  background-color: rgb(12, 53, 71);
}

.node-path {
  -webkit-animation-name: shortestPath;
          animation-name: shortestPath;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes shortestPath {
  0% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    background-color: rgb(255, 219, 88);
  }

  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgb(255, 219, 88);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgb(255, 219, 88);
  }
}

@keyframes shortestPath {
  0% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    background-color: rgb(255, 219, 88);
  }

  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgb(255, 219, 88);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgb(255, 219, 88);
  }
}

.grid {
  margin: 75px 0 0;
}

.grid-disabled {
  margin: 75px 0 0;
  pointer-events: none;
}

.nav-disabled {
  pointer-events: none;
}

.reset-button {
  position: absolute;
  right: 789px;
  top: 9px;
}

.App {
  text-align: center;
}

